<template>
	<div class="hello">
		<mu-switch label="开关:" labelLeft class="demo-switch btn" @input="change"/><br/>
		<mu-switch label="呼吸灯:" labelLeft class="demo-switch btn1" :disabled="disabled" v-model="val" @input="changesmooth"/>
		<div class="btn2">
			<span :class="{'font':disabled}">亮度:</span><mu-slider v-model="value" :step="1" :disabled="disabled" class="demo-slider slider" @change="changelight"/>
		</div>
	</div>
</template>

<script>
import $ from 'jquery'
export default {
	name: 'HelloWorld',
	data() {
		return {
			code:'close',
			disabled:true,
			val:false,
			value:0
		}
	},
	methods:{
		change(val){
			var me = this;
			switch(val){
				case true:
					me.code = 'open';
				break;
				case false:
					me.code = 'close'
				break
			}
		},
		changesmooth(val){
			var me  =this;
			switch(val){
				case true:
					me.code = "smooth";
				break;
				case false:
					me.code = 'aaaa'
				break
			}
		},
		changelight(){
			var me = this;
			if(this.value == 0){
				me.code = 'open'
			}else{
				me.code = this.value
			}
		}
	},
	watch:{
		code(){
			console.log(this.code)
			var me = this
			$.ajax({
				type:'get',
				url:'judge?code='+me.code
			})
			if(me.code == 'open'){
				me.disabled = false
			}else if(me.code == 'close'){
				me.val = false
				me.value = 0
				me.disabled = true
			}
		}
	}
}
</script>

<style scoped>
.hello{
	width:100%;
	height:50%;
	font-size:20px;
}
.hello .btn{
	cursor: pointer;
	margin-top:100px;  
	transform: translateX(50%)
}
.hello .btn1{
	cursor: pointer;
	margin-top:50px;  
	transform: translateX(50%)
}
.hello .btn2{
	cursor: pointer;
	margin-top:35px;  
	/* margin-left: */
	transform: translateX(15%)  
}
.font{
	color: rgba(0,0,0,.38);
}
.mu-slider{
	width:80% !important;
}
</style>



